import React, { useLayoutEffect, useState } from "react";
import { Typography, Button, Container, Box } from "@mui/material";
import { makeStyles } from "@mui/styles";
import bg from "../../assets/images/background2.jpg";
import resume from "../../assets/Hopansh_Gahlot_Resume_SE_2.pdf";
import "./index.css";

const useStyles = makeStyles((theme) => ({
  rootPage: {
    minHeight: "100vh",
    width: "100%",
    background: "linear-gradient(45deg, #000 10%, #0C0C0C 40%)",
    color: "white",
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "flex-start",
    position: "relative",
    overflow: "hidden",
  },
  content: {
    zIndex: 1,
    padding: theme.spacing(4),
  },
  homeBg: {
    position: "absolute",
    height: "100%",
    right: 0,
    top: 0,
    zIndex: 0,
    objectFit: "cover",
  },
  name: {
    fontSize: "3rem",
    fontWeight: "bold",
    marginBottom: theme.spacing(2),
  },
  highlightedName: {
    color: theme.palette.primary.main,
  },
  roleContainer: {
    display: "flex",
    alignItems: "center",
    marginBottom: theme.spacing(4),
  },
  changeText: {
    height: "3rem",
    overflow: "hidden",
    marginLeft: theme.spacing(1),
  },
  speciality: {
    fontSize: "2.5rem",
    fontWeight: "bold",
  },
  cvButton: {
    marginTop: theme.spacing(4),
  },
}));

const Page1 = () => {
  const classes = useStyles();
  const [isDesktop, setIsDesktop] = useState(false);

  useLayoutEffect(() => {
    function updateSize() {
      setIsDesktop(window.innerWidth >= 1024);
    }
    window.addEventListener("resize", updateSize);
    updateSize();
    return () => window.removeEventListener("resize", updateSize);
  }, []);

  return (
    <Box className={classes.rootPage}>
      {isDesktop && (
        <img src={bg} alt="Background" className={classes.homeBg} />
      )}
      <Container maxWidth="lg" className={classes.content}>
        <Typography variant="h1" className={classes.name}>
          Hello, I&apos;m{" "}
          <span className={classes.highlightedName}>Hopansh Gahlot</span>
        </Typography>
        <Box className={classes.roleContainer}>
          <Typography variant="h3">I&apos;m a</Typography>
          <Box className={classes.changeText}>
            <ul className="flip2">
              <li>Web</li>
              <li>App</li>
            </ul>
          </Box>
        </Box>
        <Typography variant="h2" className={classes.speciality}>
          Developer
        </Typography>
        <Button
          variant="contained"
          color="primary"
          size="large"
          href={resume}
          target="_blank"
          rel="noopener noreferrer"
          className={classes.cvButton}
          download
        >
          Download CV
        </Button>
      </Container>
    </Box>
  );
};

export default Page1;
